*,
*::before,
*::after {
    box-sizing: inherit;
}

html {
    box-sizing: border-box;
    font-family: $raleway;
}

body {
    min-width: 320px;
    margin: 0;
}

img {
    max-width: 100%;
    height: auto;
}

a:hover,
button:hover {
    cursor: url("../img/cursor.png"), url("../img/cursor.cur"), pointer;
}

.main {
    padding-top: 78px;
    @include media-mobile {
        padding-top: 0;
    }
    @include media-mobile-tablet {
        &--closed {
            display: none;
        }
    }
    z-index:1;
    position: relative;
}

@keyframes bounce {
    0% {
        transform: translateY(-2000px);
    }
    70% {
        transform: translateY(30px);
    }
    90% {
        transform: translateY(-10px);
    }
    100% {
        transform: translateY(0);
    }
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
    position: absolute;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    white-space: nowrap;
    border: 0;
    clip-path: inset(100%);
}