.quest {
    width: 31%;
    height: 232px;
    display: inline-block;
    margin-right: 2.2%;
    margin-bottom: 32px;
    position: relative;
    &--tomb {
        background-image: linear-gradient(to top, rgba(0, 0, 0, 0.9), rgba(255, 255, 255, 0)), url("../img/card-1@1x.jpg");
        background-repeat: no-repeat;
        background-size: cover;
        @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
            background-image: linear-gradient(to top, rgba(0, 0, 0, 0.9), rgba(255, 255, 255, 0)), url("../img/card-1@2x.jpg");
            background-size: 344px auto;
        }
    }
    &--maniac {
        background-image: linear-gradient(to top, rgba(0, 0, 0, 0.9), rgba(255, 255, 255, 0)), url("../img/card-2@1x.jpg");
        background-repeat: no-repeat;
        background-size: cover;
        @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
            background-image: linear-gradient(to top, rgba(0, 0, 0, 0.9), rgba(255, 255, 255, 0)), url("../img/card-2@2x.jpg");
            background-size: 344px auto;
        }
    }
    &--ritual {
        background-image: linear-gradient(to top, rgba(0, 0, 0, 0.9), rgba(255, 255, 255, 0)), url("../img/card-3@1x.jpg");
        background-repeat: no-repeat;
        background-size: cover;
        @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
            background-image: linear-gradient(to top, rgba(0, 0, 0, 0.9), rgba(255, 255, 255, 0)), url("../img/card-3@2x.jpg");
            background-size: 344px auto;
        }
    }
    &--ghosts {
        background-image: linear-gradient(to top, rgba(0, 0, 0, 0.9), rgba(255, 255, 255, 0)), url("../img/card-4@1x.jpg");
        background-repeat: no-repeat;
        background-size: cover;
        @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
            background-image: linear-gradient(to top, rgba(0, 0, 0, 0.9), rgba(255, 255, 255, 0)), url("../img/card-4@2x.jpg");
            background-size: 344px auto;
        }
    }
    &--mantion {
        background-image: linear-gradient(to top, rgba(0, 0, 0, 0.9), rgba(255, 255, 255, 0)), url("../img/card-5@1x.jpg");
        background-repeat: no-repeat;
        background-size: cover;
        @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
            background-image: linear-gradient(to top, rgba(0, 0, 0, 0.9), rgba(255, 255, 255, 0)), url("../img/card-5@2x.jpg");
