    }
    &--hut {
        background-image: linear-gradient(to top, rgba(0, 0, 0, 0.9), rgba(255, 255, 255, 0)), url("../img/card-6@1x.jpg");
        background-repeat: no-repeat;
        background-size: cover;
        @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
            background-image: linear-gradient(to top, rgba(0, 0, 0, 0.9), rgba(255, 255, 255, 0)), url("../img/card-6@2x.jpg");
            background-size: 344px auto;
        }
    }
    &--experiment {
        background-image: linear-gradient(to top, rgba(0, 0, 0, 0.9), rgba(255, 255, 255, 0)), url("../img/card-7@1x.jpg");
        background-repeat: no-repeat;
        background-size: cover;
        @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
            background-image: linear-gradient(to top, rgba(0, 0, 0, 0.9), rgba(255, 255, 255, 0)), url("../img/card-7@2x.jpg");
