        background-repeat: no-repeat;
        background-size: cover;
        @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
            background-image: linear-gradient(to top, rgba(0, 0, 0, 0.9), rgba(255, 255, 255, 0)), url("../img/card-8@2x.jpg");
            background-size: 344px auto;
        }
    }
    &--attic {
        background-image: linear-gradient(to top, rgba(0, 0, 0, 0.9), rgba(255, 255, 255, 0)), url("../img/card-9@1x.jpg");
        background-repeat: no-repeat;
        background-size: cover;
        @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
            background-image: linear-gradient(to top, rgba(0, 0, 0, 0.9), rgba(255, 255, 255, 0)), url("../img/card-9@2x.jpg");
            background-size: 344px auto;
        }
    }
    &--last {
        background-image: linear-gradient(to top, rgba(0, 0, 0, 0.9), rgba(255, 255, 255, 0)), url("../img/card-10@1x.jpg");
        background-repeat: no-repeat;
        background-size: cover;
        @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
            background-image: linear-gradient(to top, rgba(0, 0, 0, 0.9), rgba(255, 255, 255, 0)), url("../img/card-10@2x.jpg");
            background-size: 344px auto;
        }
    }
    &--mars {
        background-image: linear-gradient(to top, rgba(0, 0, 0, 0.9), rgba(255, 255, 255, 0)), url("../img/card-11@1x.jpg");
        background-repeat: no-repeat;
        background-size: cover;
        @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
            background-image: linear-gradient(to top, rgba(0, 0, 0, 0.9), rgba(255, 255, 255, 0)), url("../img/card-11@2x.jpg");
            background-size: 344px auto;
        }
    }
    &--hit::before {
        content: "";
        width: 262px;
        height: 63px;
        position: absolute;
        top: 15px;
        right: -178px;
        background: url("../img/badge.svg") 0 0 no-repeat;
    }
}

.quest img {
    order: 0;
}

.quest__link {
    margin: 0;
    text-decoration: none;
    color: $white;
    padding-left: 18px;
    padding-bottom: 20px;
    width: 100%;
    height: 100%;
    display: block;
    @include flex;
    flex-direction: column;
    justify-content: flex-end;
}

.quest__heading {
    font-weight: 700;
    width: 240px;
    font-size: 24px;
    line-height: 28.8px;
    margin: 0;
    color: rgba(255, 255, 255, 0.8);
    margin-bottom: 12px;
}

.quest__address {
    margin: 0;
    font-size: 13px;
    color: rgba(255, 255, 255, 0.8);
    margin-bottom: 9px;
    margin-left: 22px;
    width: 100%;
    position: relative;
    &::before {
        position: absolute;
        content: "";
        width: 11px;
        height: 14px;
        left: -19px;
        top: 0;
        background: url("../img/location-s.svg") no-repeat 0 0;
    }
}

.quest__people {
    color: rgba(255, 255, 255, 0.8);
    margin-left: 25px;
    margin-right: 34px;
    font-size: 13px;
    position: relative;
    &::before {
        position: absolute;
        content: "";
        width: 13px;
        height: 16px;
        right: 53px;
        top: 0;
        background: url("../img/person-s.svg") no-repeat 0 0;
    }
    &:after {
        position: absolute;
        content: "";
        width: 1px;
        height: 20px;
        right: -13px;
        top: -4px;
        background-color: rgba(255, 255, 255, 0.5);
    }
}

.quest__difficulty {
    color: rgba(255, 255, 255, 0.8);
    margin-left: 10px;
    width: 81px;
    font-size: 13px;
    position: relative;
    &::before {
        position: absolute;
        content: "";
        width: 16px;
        height: 16px;
        left: -21px;
        top: 0;
        background: url("../img/puzzle-s.svg") no-repeat 0 0;
    }
}

@include media-mobile {
    .quest {
        width: 100%;
        margin-right: 0;
    }
}