    .description__container {
        padding: 0 136px;
        padding-top: 107px;
        padding-bottom: 147px;
        margin: 0 auto;
    }
    
    .description__heading {
        font-weight: 800;
        font-size: 64px;
        color: $white;
        line-height: 70.4px;
        width: 540px;
        margin: 0;
        margin-bottom: 35px;
    }
    
    .description__text {
        font-weight: 500;
        font-size: 17px;
        line-height: 25.5px;
        margin: 0;
        margin-bottom: 90px;
        width: 482px;
        color: $white;
        letter-spacing: 0.02em;
    }
    
    .description__button {
        color: $white;
        text-transform: uppercase;
        font-size: 17px;
        background-color: $yellow;
        line-height: 20px;
        letter-spacing: 0.03em;
        font-weight: 800;
        padding: 22px 51px;
        border-radius: 40px;
        text-decoration: none;
    }
    
    @include media-mobile-tablet {
        .description {
            padding-top: 361px;
            padding-bottom: 64px;
        }
        .description__container {
            padding: 0 30px;
            @include flex;
            justify-content: center;
        }
        .description__heading {
            font-size: 36px;
            line-height: 39.6px;
            text-align: center;
            margin-bottom: 32px;
            width: 100%;
        }
        .description__button {
            font-size: 14px;
            padding: 14px 32px;
            margin: 0 auto;
        }
        .description__text {
            display: none;
        }
    }