.modal-city--closed {
    display: none;
}

.modal-city__overlay {
    position: fixed;
    z-index: 99;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    overflow: hidden;
    overflow-y: auto;
    justify-content: center;
    align-items: center;
    padding: 30px 0;
    background-color: rgba(61, 51, 51, 0.96);
    -webkit-overflow-scrolling: touch;
    flex-flow: column nowrap;
}

.modal-city__container {
    position: relative;
    width: 480px;
    margin: 0 auto;
    padding: 32px 32px 53px 32px;
    background: linear-gradient(0deg, #141414 0%, #1F1D1D 100%);
    animation: bounce 0.6s;
    flex-shrink: 0;
}

.modal-city__heading {
    font-size: 32px;
    line-height: 38.4px;
    font-weight: 800;
    color: $white;
    margin: 0;
    margin-bottom: 24px;
}

.modal-city__button-close {
    position: absolute;
    border: none;
    opacity: 0.5;
    outline: none;
    background-color: rgba(0, 0, 0, 0);
    width: 14px;
    height: 14px;
    background-image: url('../img/cross.svg');
    background-position: center center;
    background-repeat: no-repeat;
    top: 40px;
    right: 32px;
    &:focus,
    &:active,
    &:hover {
        opacity: 1;
    }
}

.modal-city__location {
    margin-left: 35px;
    position: relative;
    color: rgba(255, 255, 255, 0.32);
    font-weight: 400;
    font-size: 17px;
    line-height: 27.2px;
    text-decoration: none;
    &:after {
        position: absolute;
        content: "";
        width: 100%;
        height: 2px;
        background-color: rgba(255, 255, 255, 0.32);
        bottom: -2px;
        left: 0;
    }
    &::before {
        position: absolute;
        content: "";
        width: 24px;
        height: 24px;
        background: url('../img/gps.svg') 0 0 no-repeat;
        top: 0;
        left: -35px;
    }
}

.modal-city__text {
    margin-top: 40px;
    width: 100%;
    display: block;
    color: $white-t;
    margin-bottom: 16px;
}

.modal-city__list {
    @include list;
}

.modal-city__item {
    margin-bottom: 20px;
}

.modal-city__link {
    text-decoration: none;
    color: rgba(255, 255, 255, 0.32);
    font-weight: 700;
    font-size: 24px;
    line-height: 28.8px;
    &--active {
        position: relative;
        &::before {
            position: absolute;
            content: "";
            width: 100%;
            height: 2px;
            background-color: $yellow;
            bottom: -4px;
        }
    }
    &:hover,
    &:active,
    &:focus {
        color: $white;
    }
}

@include media-mobile {
    .modal-city__overlay {
        justify-content: flex-start;
        position: absolute;
        padding: 0;
    }
    .modal-city__container {
        width: 100%;
        padding: 32px 16px 47px 16px;
    }
}