    .modal-question--closed {
        display: none;
    }
    
    .modal-question__overlay {
        min-width: 320px;
        position: fixed;
        z-index: 99;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        display: flex;
        overflow: hidden;
        overflow-y: auto;
        justify-content: center;
        align-items: center;
        padding: 30px 0;
        background-color: rgba(61, 51, 51, 0.96);
        -webkit-overflow-scrolling: touch;
        flex-flow: column nowrap;
    }
    
    .modal-question__container {
        position: relative;
        width: 480px;
        margin: 0 auto;
        padding: 32px;
        background: linear-gradient(0deg, #141414 0%, #1F1D1D 100%);
        animation: bounce 0.6s;
        flex-shrink: 0;
    }
    
    .modal-question__button-close {
        position: absolute;
        border: none;
        opacity: 0.5;
        outline: none;
        background-color: rgba(0, 0, 0, 0);
        width: 14px;
        height: 14px;
        background-image: url('../img/cross.svg');
        background-position: center center;
        background-repeat: no-repeat;
        top: 40px;
        right: 32px;
        &:focus,
        &:active,
        &:hover {
            opacity: 1;
        }
    }
    
    .modal-question__heading {
        font-size: 32px;
        line-height: 38.4px;
        font-weight: 800;
        color: $white;
        margin: 0;
        margin-bottom: 40px;
    }
    
    .modal-question ul {
        @include list;
    }
    
    .modal-question label {
        position: relative;
        color: $white;
        font-weight: 500;
        font-size: 14px;
        line-height: 22.4px;
        margin-bottom: 17px;
        display: block;
        letter-spacing: 0.03em;
    }
    
    .modal-question input {
        width: 400px;
        background-color: rgba(0, 0, 0, 0);
        border: solid 1px $white-t;
        padding: 16px 24px;
        margin-bottom: 30px;
    }
    
    .modal-question textarea {
        width: 400px;
        height: 120px;
        background-color: rgba(0, 0, 0, 0);
        border: solid 1px $white-t;
        padding: 16px 24px;
        margin-bottom: 24px;
    }
    
    .modal-question__form-container {
        @include flex;
    }
    
    .modal-question label[for="modal-checkbox"] {
        width: 380px;
        margin-left: 24px;
        order: 2;
        font-size: 14px;
        font-weight: 500;
        line-height: 20.16px;
        letter-spacing: 0;
        margin-bottom: 0;
    }
    
    .modal-question input:valid {
        border-color: #2cdb52;
    }
    
    .modal-question .input--valid {
        border-color: #2cdb52;
    }
    
    .modal-question .input--invalid {
        border-color: $yellow;
    }
    
    .modal-question__error-message {
        font-weight: 500;
        font-size: 12px;
        line-height: 17px;
        color: $yellow;
        display: block;
        margin-top: -27px;
        margin-bottom: 27px;
        &--hidden {
            display: none;
        }
    }
    
    .modal-question__rules {
        color: $white;
    }
    
    .modal-question input[type="checkbox"]+.modal-question__checkbox-indicator {
        position: absolute;
        top: 0;
        bottom: 15px;
        left: -24px;
        width: 16px;
        height: 16px;
        border: 1px solid rgba(0, 0, 0, 0);
        border-radius: 5px;
        background-color: rgba(242, 137, 15, 0.5);
        cursor: pointer;
        outline: none;
    }
    
    .modal-question input[type="checkbox"]:checked+.modal-question__checkbox-indicator {
        background-image: url("../img/tick.svg");
        background-position: center center;
        background-repeat: no-repeat;
        background-color: $yellow;
    }
    
    .modal-question__form-container button {
        background-color: #b8b8b8;
        color: $white;
        text-transform: uppercase;
        padding: 15px 32px;
        margin: 0 auto;
        display: block;
        font-size: 14px;
        font-weight: 800;
        letter-spacing: 0.03em;
        border: none;
        border-radius: 20px;
        margin-bottom: 22px;
        &:hover,
        &:active,
        &:focus {
            background-color: $yellow;
        }
    }
    
    @include media-mobile {
        .modal-question__overlay {
            justify-content: flex-start;
            position: absolute;
            padding: 0;
        }
        .modal-question__container {
            width: auto;
            margin: 0 auto;
            padding: 32px 16px 47px 16px;
        }
        .modal-question input {
            width: 100%;
            max-width: 480px;
        }
        .modal-question textarea {
            width: 100%;
            max-width: 480px;
        }
        .modal-question label[for="modal-checkbox"] {
            width: 100%;
            max-width: 480px;
        }
    }