.socials__list {
    @include list;
    @include flex;
    justify-content: space-between;
}

.socials__item {
    width: 28px;
    height: 28px;
    margin: 0;
    margin-right: 7px;
    text-decoration: none;
    &:last-of-type {
        margin-right: 0;
    }
    &--instagram {
        background: url("../../img/instagram-gray.svg") 0 0 no-repeat;
        &:hover,
        &:focus,
        &:active {
            background: url("../../img/instagram.svg") 0 0 no-repeat;
            cursor: url("../img/cursor.png"), url("../img/cursor.cur"), pointer;
        }
    }
    &--vk {
        background: url("../../img/vk-gray.svg") 0 0 no-repeat;
        &:hover,
        &:focus,
        &:active {
            background: url("../../img/vk.svg") 0 0 no-repeat;
            cursor: url("../img/cursor.png"), url("../img/cursor.cur"), pointer;
        }
    }
    @include media-mobile-tablet {
        width: 36px;
        height: 36px;
        margin-right: 15px;
        &:last-of-type {
            margin-right: 0;
        }
        &--instagram {
            background: url("../../img/instagram-m.svg") 0 0 no-repeat;
        }
        &--vk {
            background: url("../../img/vk-m.svg") 0 0 no-repeat;
        }
    }
}