                .info__container {
                    width: 80%;
                    margin: 0 auto;
                    max-width: 1005px;
                    padding: 72px 20px 0 38px;
                    @include flex;
                    justify-content: flex-end;
                }
                
                .info__genre {
                    font-size: 14px;
                    line-height: 20px;
                    font-weight: 500;
                    color: $yellow;
                    display: block;
                    text-transform: lowercase;
                    margin-bottom: 9px;
                    margin-left: 9px;
                }
                
                .info__heading {
                    margin: 0;
                    font-weight: 900;
                    font-size: 92px;
                    letter-spacing: -0.03em;
                    line-height: 87.4px;
                    color: $white;
                    text-transform: uppercase;
                    margin-bottom: 29px;
                    letter-spacing: -0.03em;
                }
                
                .info__main-list {
                    @include list;
                    width: 565px;
                }
                
                .info__main-item {
                    &--maniac {
                        display: block;
                    }
                    &--mantion {
                        display: none;
                    }
                    &--ritual {
                        display: none;
                    }
                    &--experiment {
                        display: none;
                    }
                }
                
                .info__list {
                    @include list;
                    @include flex;
                    width: 440px;
                    height: 36px;
                    justify-content: space-between;
                    margin-left: 31px;
                    margin-bottom: 19px;
                    align-items: center;
                }
                
                .info__item {
                    font-size: 14px;
                    line-height: 20px;
                    font-weight: 400;
                    color: $white;
                    position: relative;
                    padding-left: 30px;
                    padding-right: 25px;
                    border-right: 1px solid $white;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    &:last-of-type {
                        border-right: none;
                    }
                    &::before {
                        position: absolute;
                        content: "";
                        width: 24px;
                        height: 24px;
                        left: 0px;
                        top: 6px;
                    }
                    &--time::before {
                        background: url(../img/clock.svg) no-repeat 0 0;
                    }
                    &--people::before {
                        height: 23px;
                        background: url(../img/person.svg) no-repeat 0 0;
                    }
                    &--difficulty::before {
                        background: url(../img/puzzle.svg) no-repeat 0 0;
                    }
                }
                
                .info__text {
                    width: 521px;
                    font-weight: 500;
                    font-size: 15px;
                    line-height: 23px;
                    color: $white-t;
                    margin: 0;
                    margin-bottom: 40px;
                    margin-left: 32px;
                    letter-spacing: 0.013em;
                }
                
                .info__text--br-t {
                    display: none;
                }
                
                .info__button {
                    display: block;
                    font-size: 17px;
                    font-weight: 800;
                    letter-spacing: 0.03em;
                    line-height: 20px;
                    color: $white;
                    background-color: $yellow;
                    width: 250px;
                    text-align: center;
                    padding: 22px;
                    text-decoration: none;
                    text-transform: uppercase;
                    border: none;
                    border-radius: 40px;
                    margin-left: 33px;
                    &:hover,
                    &:active,
                    &:focus {
                        background-color: $yellow-hover;
                    }
                }
                
                .info__button-all {
                    display: none;
                }
                
                @include media-mobile-tablet {
                    .info__container {
                        padding: 0 16px;
                        padding-top: 343px;
                        padding-bottom: 28px;
                        width: auto;
                        position: relative;
                        justify-content: center;
                    }
                    .info__genre {
                        font-size: 14px;
                        line-height: 20px;
                        font-weight: 500;
                        margin-bottom: 9px;
                        margin-left: 0;
                    }
                    .info__heading {
                        font-size: 42px;
                        line-height: 46px;
                        margin-bottom: 17px;
                        letter-spacing: 0.01em;
                    }
                    .info__list {
                        width: 97%;
                        max-width: 600px;
                        margin: 0;
                        margin-left: 2%;
                        height: 48px;
                        margin-bottom: 32px;
                    }
                    .info__item {
                        width: 36%;
                        height: 48px;
                        line-height: 17.3px;
                        text-align: center;
                        padding: 0;
                        padding: 0 25px;
                        padding-top: 32px;
                        align-items: flex-start;
                        justify-content: center;
                        &:first-of-type {
                            padding-left: 0;
                            width: 30%;
                            &:before {
                                left: 27%;
                            }
                        }
                        &:last-of-type {
                            padding-right: 0;
                            width: 30%;
                        }
                        &::before {
                            left: 43%;
                            top: 0;
                        }
                    }
                    .info__text {
                        width: 100%;
                        max-width: 600px;
                        margin: 0 auto;
                        letter-spacing: -0.01em;
                        margin-bottom: 21px;
                    }
                    .info__main-list {
                        width: auto;
                    }
                    .info__button {
                        margin-left: 0;
                    }
                    .info__button-all {
                        display: block;
                        text-decoration: none;
                        position: absolute;
                        color: $white;
                        font-size: 12px;
                        font-weight: 700;
                        letter-spacing: 0.02em;
                        line-height: 17.28px;
                        top: 34px;
                        left: 30px;
                        text-transform: uppercase;
                        &:hover,
                        &:active,
                        &:focus {
                            opacity: 0.5;
                        }
                        &::before {
                            content: "";
                            position: absolute;
                            width: 7px;
                            height: 11px;
                            top: 1px;
                            left: -15px;
                            background: url('../img/arrow-left.svg') 0 0 no-repeat;
                        }
                    }
                }
                
                @include media-tablet {
                    .info__item:first-of-type::before {
                        left: 34%;
                    }
                    .info__list {
                        margin-left: auto;
                        margin-right: auto;
                    }
                }